#search-topic-terminal {
  transform: translate(calc(-50% - 100px), calc(-50% - 100px));
}
#search-topic-terminal #search-topic-terminal__info {
  background: #fff;
  color: #000;
  padding: 1em;
  border-right: 1.25em solid #000;
}
#search-topic-terminal #search-topic-terminal__info .menu__options ul {
  padding-left: 1ch !important;
}
#search-topic-terminal #search-topic-terminal__info .menu__options ul .menu-button button {
  cursor: pointer;
}