header {
  position: absolute;
  top: 2.5em;
  width: calc(100% - 2 * 2.5em);
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header button {
  appearance: none;
  background: none;
  border: none;
  outline: none;
  padding: 0;
  cursor: pointer;
}
header div {
  display: flex;
  align-items: center;
}
header div img {
  margin-right: 1.25em;
}
header nav ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
  list-style: none;
  padding: 0;
}
header nav ul li + li {
  padding-left: 1.875em;
}