.input {
  display: flex;
}
.input div {
  position: relative;
}
.input div input {
  position: absolute;
  height: 0;
  width: 0;
  background: none;
  border: none;
  outline: none;
  padding: 0;
  opacity: 0;
  margin: 0;
  caret-color: rgba(255, 255, 255, 0);
}
.input div input::placeholder {
  color: #2eaa09;
}
.input div input:focus::placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.input div .input__value {
  color: #2eaa09;
  margin-left: 1.25em;
}
.input div .input__value::after {
  content: "0";
  height: 21px;
  width: 1ch;
  color: white;
  background-color: white;
  opacity: 0;
}
.input div input:focus + .input__value::after {
  animation: blink 1s step-end infinite;
}
@keyframes blink {
  from, to {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}