#test-terminal .terminal__body p {
  flex: 1;
  line-height: 200%;
}
#test-terminal .terminal__body p .word--incorrect span {
  color: #c91313;
  text-decoration: line-through;
}
#test-terminal .terminal__body p .word--incorrect span:last-child {
  text-decoration: none;
}
#test-terminal .terminal__body p .char {
  color: rgba(255, 255, 255, 0.5);
}
#test-terminal .terminal__body p .char--current {
  text-decoration: none !important;
  border-bottom: 2px solid white;
  animation: char--current__blink 1s step-end infinite;
}
@keyframes char--current__blink {
  from, to {
    border-bottom-color: white;
  }
  50% {
    border-bottom-color: transparent;
  }
}
#test-terminal .terminal__body p .char--correct {
  color: white;
}
#test-terminal .terminal__body p .char--incorrect {
  color: #c91313;
  background-color: rgba(201, 19, 19, 0.5);
}
#test-terminal .terminal__body p .br::after {
  content: " ";
  display: block;
  height: 1em;
}
#test-terminal .terminal__body #test-terminal__canvas_container {
  height: 50%;
}
#test-terminal .terminal__body #test-terminal__text_container {
  height: calc(100% - 2.5rem - 1.125rem - 2.5em - 1.25em);
  overflow-y: scroll;
}
#test-terminal .terminal__body #test-terminal__text_container[game-mode=true] {
  height: calc(100% - 2.5rem - 1.125rem - 2.5em - 1.25em - 50% - 1.25em);
}
#test-terminal .terminal__body #test-terminal__results_panel {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
#test-terminal .terminal__body #test-terminal__results_panel div {
  display: flex;
  flex-direction: column;
  align-items: center;
}