.menu-checkbox {
  display: flex;
}
.menu-checkbox input {
  height: 0;
  width: 0;
  margin-left: 1.25em;
}
.menu-checkbox input[value=true] + .menu-checkbox__checked {
  color: #2eaa09;
}
.menu-checkbox input[value=false] + .menu-checkbox__checked {
  color: #c91313;
}
.menu-checkbox p:last-child {
  cursor: pointer;
}