#road-map-terminal .terminal__body {
  display: flex;
  flex-direction: column;
}
#road-map-terminal .terminal__body section h1 {
  font-size: clamp(1.2rem, 1.125vw, 1.125rem);
  color: #2eaa09;
}
#road-map-terminal .terminal__body section ul {
  padding: 0 2ch;
}
#road-map-terminal .terminal__body section p,
#road-map-terminal .terminal__body section li {
  margin-top: 2.5em;
  margin-top: 1.125em;
  max-width: 60ch;
}
#road-map-terminal .terminal__body section p b,
#road-map-terminal .terminal__body section li b {
  color: #2eaa09;
}
#road-map-terminal .terminal__body section + section {
  margin-top: 2.5em;
}